  async conectarLed() {
    console.log("Conectandose...");
    try {
      this.device = await navigator.hid.requestDevice({
        filters: [{
          vendorId: 0x20a0,
          productId: 0x41e5,
        }]
      });
      //device.open();
      console.log(this.device);
      await this.device.open();
    } catch (err) {
      // No device was selected.
    }

    if (this.device !== undefined) {
      // Add |device| to the UI.
    }

  }

